*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
}

/* end of reset */

body {
  font-family: Helvetica, Arial, system-ui;
  height: 100vh;
  line-height: 1.5;
}

button {
  cursor: pointer;
  border: none;
  text-align: left;
  transition: transform 0.3s ease-in-out;
  will-change: transform;
}

button:hover {
  transform: scale(1.01) translateZ(0);
}

input,
textarea,
select {
  padding: 5px;
}

#container {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: auto 1fr;
  height: 100vh;
}

#header {
  min-height: 10vh;
  grid-column: 1/-1;
  grid-row: 1/2;
}

#logo {
  position: absolute;
  top: 2%;
  left: 2%;
  width: 100px;
  height: auto;
}

#left-sidebar {
  position: relative;
  margin: 10px;
}

#projects {
  position: relative;
  min-height: 80vh;
  padding: 10px;
  margin: 0 10px;
  z-index: 0;
}

#projects::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 10px;
  opacity: 0.1;
  mix-blend-mode: overlay;
  z-index: -1;
}

.project-button {
  display: block;
  padding: 5px;
  width: 80%;
  margin-bottom: 10px;
}

.add-new-project {
  width: 80%;
}

#tasks {
  min-height: 80vh;
  padding: 10px;
}

.task-main {
  padding: 10px;
  margin-bottom: 10px;
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
  will-change: transform;
}

.task-main::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.4);
  border-radius: 10px;
  opacity: 0.1;
  mix-blend-mode: overlay;
  z-index: -1;
}

.task-main:hover {
  transform: scale(1.01);
}

.task-description {
  padding-bottom: 10px;
}

.task-sub {
  display: flex;
  justify-content: flex-start;
  gap: 20%;
  padding: 5px;
}


#task-modal, #confirm-remove-task-modal {
  position: fixed;
  padding: 10px;
  top: 50%;
  left: 50%;
  width: auto;
  background: rgba(0, 0, 0, 0.1);
  transform: translate(-50%, -50%);
  gap: 15px;
  border-radius: 8px;
  border: none;
  backdrop-filter: blur(1px);
}

#task-form {
  display: flex;
  flex-direction: column;
  padding: 25px;
  gap: 15px;
}

#task-form label {
  display: block;
  font-weight: bold;
}

#task-form input,
#task-form textarea,
#task-form select {
  width: 100%;
}

#task-form textarea {
  resize: vertical;
  min-height: 80px;
}

#close-modal-button, .remove-task-button {
  padding: 5px;
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
}

#save-task-button, #confirm-remove-button {
  padding: 5px;
  background: none;
  border: none;
  font-weight: bold;
}

#right-sidebar {
  margin: 10px;
}

#due-dates {
  position: relative;
  min-height: 80vh;
  padding: 10px;
  z-index: 0;
}

#due-dates::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 10px;
  opacity: 0.1;
  mix-blend-mode: overlay;
  z-index: -1;
}
